import React from 'react';
import { getThread, sendToRender } from './api';
import PickComments from './PickComments/index';
import Question from './Question';
import SelectThread from './SelectThread';
import Settings from './Settings';
import { connect } from 'react-redux'
import { setComments, setQuestion, setStage, setOptions, nextStage } from './redux/actions'
import './App.css';

class App extends React.Component {
	constructor(props) {
		super(props)
		this.state = { stage: 0, comments: [], question: null, processed: null, options: { song: null } }
	}
	fetchThread(thread) {
		getThread(thread)
			.then(json => {
				let mapping = comment => {
					let newcomment = Object.assign({}, comment)

					if (Array.isArray(newcomment.replies)) {
						newcomment.replies = newcomment.replies.map(mapping)
					}

					return {
						enabled: false,
						data: newcomment
					}
				}

				let comments = json[1].map(mapping)

				this.props.setQuestion(json[0])
				this.props.setComments(comments)
				this.props.setStage(1)
			})
	}
	addSettings(options) {
		this.props.setOptions(options)
		this.props.nextStage()

		sendToRender(this.props.question, this.props.processed, options)
	}
	render() {
		return <div className="App">
			{this.props.stage === 0 ?
				<SelectThread onSelection={this.fetchThread.bind(this)} /> :
				this.props.stage === 1 ?
					<Question /> :
					this.props.stage === 2 ?
						<PickComments /> :
						this.props.stage === 3 ?
							<Settings onSelection={this.addSettings.bind(this)} /> :
							this.props.stage === 4 ?
								<div>
									<h1>rendering atm bro</h1>
									<p>You may close this tab</p>
								</div> :
								null
			}
			<br />
			{this.props.stage !== 0 &&
				<button style={{ fontSize: 12 }} onClick={() => this.props.setStage(this.props.stage - 1)}>GO BACK!</button>
			}
		</div>
	}
}

let mapStateToProps = store => {
	return {
		question: store.question,
		processed: store.processed,
		stage: store.stage,
		options: store.options,
	}
}

export default connect(mapStateToProps, {
	setComments,
	setQuestion,
	setStage,
	nextStage,
	setOptions,
})(App);