import React from 'react'
import Comment from './Comment'
import './index.css'
import { estimateCommentHtml, getFull } from '../estimate'

import FakeComment from './FakeComment';

import { connect } from 'react-redux'
import { setProcessed, setComments, nextStage } from '../redux/actions'

function recFilter(commentArr) {
	let news = commentArr.filter(h => h.enabled)
		.map(d => d.data)
		.map(d => {
			let newS = Object.assign({}, d)
			if (newS.replies) {
				newS.replies = recFilter(newS.replies)
			}
			return newS
		})
	return news
}

class PickComments extends React.Component {
	constructor(props) {
		super(props)

		this.state = { current: 0, tooTall: false, height: 0 }
		this.onSubmit = this.onSubmit.bind(this)
		this.handle = this.handle.bind(this)
		this.toggleCurrent = this.toggleCurrent.bind(this)
		this.handleKeyEvent = this.handleKeyEvent.bind(this)

		this.fakecomments = React.createRef()
	}
	handleKeyEvent(e) {
		switch (e.code) {
			case "KeyA":
			case "ArrowLeft":
				this.goPrev()
				break;
			case "KeyD":
			case "ArrowRight":
				this.goNext()
				break
			case "Enter":
				this.onSubmit()
				break
			default:
				if (e.code.indexOf('Digit') === 0) {
					let num = parseInt(e.code.replace('Digit', ''))
					this.toggleCurrent(num)
				}
		}
	}
	componentDidMount() {
		window.addEventListener('keydown', this.handleKeyEvent)
	}
	goPrev() {
		if (this.state.current > 0) {
			this.setState({ current: this.state.current - 1 })
		}
	}
	goNext() {
		if (this.state.current < this.props.comments.length - 1) {
			this.setState({ current: this.state.current + 1 })
		}
	}
	onSubmit() {
		let filt = recFilter(this.props.comments)

		this.props.setProcessed(filt)
		this.props.nextStage()
	}
	handle(id) {
		if (this.state.chosen.has(id)) {
			this.state.chosen.set(id)
		} else {
			this.state.chosen.add(id)
		}
		this.setState({
			chosen: this.state.chosen,
		})
	}
	toggleCurrent(commentNum) {
		let set = this.props.comments

		if (commentNum) {
			let currentNum = 0

			// Used for checking the N:th visible comment in the tree
			function traverseOnce(comment) {
				currentNum++
				if (currentNum === commentNum) {
					return comment
				}
				if (Array.isArray(comment.data.replies)) {
					for (let i = 0; i < comment.data.replies.length; i++) {
						const element = comment.data.replies[i];
						let res = traverseOnce(element)
						if (res) {
							return res
						}
					}
				}
			}

			let reqComment = traverseOnce(set[this.state.current])
			if (!reqComment) {
				return // Could not find a comment with inputted number
			}
			reqComment.enabled = !reqComment.enabled
		} else {
			let opt = set[this.state.current]
			opt.enabled = !opt.enabled
			set[this.state.current] = opt
		}

		// Rerender and update estimated height
		this.forceUpdate()
		this.forceUpdate()

		this.props.setComments(set)
	}
	toMinSec(seconds) {
		let fullMins = Math.floor(seconds / 60) // No. full mins
		let restSecs = ('00' + Math.round(seconds - 60 * fullMins)).slice(-2)
		let mins = ('00' + fullMins).slice(-2)
		return `${mins}:${restSecs}`
	}
	calculateTotalTime() {
		let recAdd = comment => {
			let fullHtml = comment.data.body_html

			if (Array.isArray(comment.data.replies)) {
				fullHtml += comment.data.replies
					.filter(r => r.enabled)
					.map(recAdd)
					.reduce((a, b) => a + b, '')
			}

			return fullHtml
		}
		let commentTimes = this.props.comments
			.filter(d => d.enabled)
			.map(recAdd)
			.map(b => estimateCommentHtml(b).estimate)

		return Math.round(1e2 * getFull(commentTimes)) / 1e2
	}
	onCurrentCommentChange(comment) {
		// let set = this.props.comments

		// let data = set[this.state.current].data
		// data.body_html = e
		// set[this.state.current].data = data

		// this.setState({ settings: set })
		this.props.setComments(this.props.comments)
	}
	componentDidUpdate() {
		let height = this.fakecomments.current.clientHeight
		let max = 1080 / 2.4
		this.state.tooTall = height > max
		this.state.height = height
	}
	render() {
		let comment = this.props.comments[this.state.current]
		let selectedComments = this.props.comments.filter(d => d.enabled).length
		// let { estimate, words, splits } = estimateCommentHtml(comment.data.body_html)
		return (
			<div className="Comments">
				<div>
					<div className="top-comment">
						<Comment key={comment.data.id} comment={comment} onChange={this.onCurrentCommentChange.bind(this)} />
					</div>
					<div className="options">
						<label>
							<input type="checkbox" checked={comment.enabled} onChange={() => this.toggleCurrent.bind(this)(1)} />
							Enable
						</label>
						<div className={this.state.tooTall ? 'tooTall' : ''}>Height: {this.state.height} (max=450)</div>
					</div>
					<div className="controls">
						<button disabled={this.state.current <= 0} onClick={this.goPrev.bind(this)}>Prev</button>
						<input style={{ width: 60 }} type="number" max={this.props.comments.length} min={1} value={this.state.current + 1} onChange={e => this.setState({ current: Math.max(0, parseInt(e.target.value) - 1 || 0) })} />
						<button disabled={this.state.current > this.props.comments.length} onClick={this.goNext.bind(this)}>Next</button>
					</div>
					<div className="controls">
						<span>Total time: {this.toMinSec(this.calculateTotalTime())}, {selectedComments} {selectedComments === 1 ? 'comment' : 'comments'} selected</span>
						<button onClick={this.onSubmit}>Finish</button>
					</div>
				</div>
				<div className="fake-container" ref={this.fakecomments}>
					<FakeComment comment={comment} />
				</div>
			</div>
		)
	}
}

export default connect(state => ({
	comments: state.comments,
}), { setProcessed, setComments, nextStage })(PickComments);