import React from 'react'
import { Editor, EditorState, ContentState, convertFromHTML, CompositeDecorator } from 'draft-js'
import { stateToHTML } from 'draft-js-export-html'
import { foulSpanArray } from '../sanitize'

function handleStrategy(contentBlock, callback, contentState) {
	foulSpanArray.forEach(reg => {
		findWithRegex(new RegExp(reg, 'ig'), contentBlock, callback);
	})
}

function editStrategy(contentBlock, callback) {
	findWithRegex(/(?:^|\W)edit/img, contentBlock, callback);
}

let handleSpan = props => {
	return <span className="highlight-danger">{props.children}</span>
}

let editSpan = props => {
	return <span className="highlight-warn">{props.children}</span>
}

function findWithRegex(regex, contentBlock, callback) {
	const text = contentBlock.getText();
	let matchArr, start;
	while ((matchArr = regex.exec(text)) !== null) {
		start = matchArr.index;
		callback(start, start + matchArr[0].length);
	}
}

let decorators = new CompositeDecorator([
	{
		strategy: handleStrategy,
		component: handleSpan,
	},
	{
		strategy: editStrategy,
		component: editSpan,
	},
])

class Comment extends React.Component {
	constructor(p) {
		super(p)

		let d = convertFromHTML(p.comment.data.body_html)
		this.state = { html: EditorState.createWithContent(ContentState.createFromBlockArray(d.contentBlocks, d.entityMap), decorators) }
	}
	handlePress(e) {
		e.stopPropagation()
	}
	propagateHtmlChange(val) {
		this.setState({ html: val })

		let html = stateToHTML(this.state.html.getCurrentContent())
		this.props.comment.data.body_html = html

		this.props.onChange()
	}
	propagateEnableChange() {
		this.props.comment.enabled = !this.props.comment.enabled
		this.props.onChange()
	}
	render() {
		let { comment, onChange } = this.props

		return (
			<div className="Comment">
				<input className="left-box" type="checkbox" checked={comment.enabled} onChange={this.propagateEnableChange.bind(this)} />
				<div onKeyDown={this.handlePress}>
					<div onClick={this.propagateEnableChange.bind(this)}>
						<span style={{ color: "blue", marginRight: 5 }}>{comment.data.author}</span>
						<span>{comment.data.score > 1e3 ? Math.round(comment.data.score / 1e2) / 1e1 + 'k' : comment.data.score} points</span>
						<span className="gilding">{comment.data.gildings.gid_3 > 0 &&
							<img src="https://www.redditstatic.com/desktop2x/img/gold/badges/award-platinum-cartoon.png" alt="platinum" width={16} />
						}{comment.data.gildings.gid_3 > 1 && comment.data.gildings.gid_3}</span>
						<span className="gilding">{comment.data.gildings.gid_2 > 0 &&
							<img src="https://www.redditstatic.com/desktop2x/img/gold/badges/award-gold-cartoon.png" alt="gold" width={16} />
						}{comment.data.gildings.gid_2 > 1 && comment.data.gildings.gid_2}</span>
						<span className="gilding">{comment.data.gildings.gid_1 > 0 &&
							<img src="https://www.redditstatic.com/desktop2x/img/gold/badges/award-silver-cartoon.png" alt="silver" width={16} />
						}{comment.data.gildings.gid_1 > 1 && comment.data.gildings.gid_1}</span>
					</div>
					<Editor
						// style={{ display: 'inline-block' }}
						editorState={this.state.html}
						onChange={this.propagateHtmlChange.bind(this)} />
					{Array.isArray(comment.data.replies) && comment.data.replies.map(d => <Comment key={d.data.id} comment={d} onChange={onChange} />)}
				</div>
			</div>
		)
	}
}

export default Comment;